// ==============================================
// Post content / Markdown body
// ==============================================

.post-content {
  overflow: hidden;
  line-height: 1.5;
  word-wrap: break-word;

  > *:first-child {
    margin-top: 30px !important;
  }

  > *:last-child {
    margin-bottom: 0 !important;
  }
}

.post-content h2,
.post-content h3,
.section-heading {
  margin: 40px 0 30px;
  font-weight: 700;
}

.post-content h4,
.post-content h5,
.post-content h6 {
  margin: 40px 0 30px;
  font-weight: 600;
  color: @m-gray;
}

.post-content h2 {
  font-size: 40px;
}

.post-content h3,
.section-heading {
  font-size: 36px;
}

.post-content h4 {
  font-size: 30px;
}

.post-content h5 {
  font-size: 24px;
}

.post-content h6 {
  font-size: 22px;
}

.post-content h2,
.post-content h3,
.section-heading,
.post-content h4,
.post-content h5,
.post-content h6 {
  a {
    text-decoration: underline;
    color: inherit;
  }
}

blockquote,
.post-content ul,
.post-content ol,
.post-content dl,
.table-responsive,
pre,
figure {
  margin: @m-margin;
}

figcaption,
.caption {
  display: block;
  margin: 0;
  padding: 10px;
  font-style: italic;
  font-size: 14px;
  color: @gray-light;
  text-align: center;
  border-bottom-right-radius: 5px;
  border-bottom-left-radius: 5px;
}

// list, blockquote, figcaption, copyright anchor
.post-content li,
figcaption,
.caption {
  a {
    text-decoration: underline;
  }
}

blockquote,
figcaption,
.caption,
.copyright {
  a {
    color: inherit;
  }
}

.post-content hr,
.section-divider {
  display: block;
  max-width: 100px;
  margin: 51px auto 41px;
  border: 0;
  border-top: 1px solid rgba(0, 0, 0, 0.15);
}

// GitHub Pages and Markdown
// -------------------------------

// Emoji
.emoji {
  display: inline-block;
  max-width: none;
}

// Footnotes
.footnotes {
  margin: @m-margin;
  font-size: 16px;

  ol,
  p {
    margin: 15px 0;
  }

  &:before {
    content: 'Footnotes';
    font-weight: 700;
    .m-sans-serif;
  }
}

// Footnotes anchor link
.footnote,
.reversefootnote {
  &:link {
    text-decoration: none;
  }
}

// ==============================================
//  Page content
// ==============================================

// Middot divider
.middot-divider {
  display: inline-block;
  padding: 0 0.42em;
}

.middot-divider:after {
  content: '\00b7';
}

// Social
.social-container,
.comments {
  margin: 50px 0 40px;
}

.social-container a {
  color: #e1e8ed; // Twitter brand

  &:hover,
  &:focus {
    color: #55acee;
  }
}

// Archives page
.archives {

  > *:first-child {
    margin-top: 30px !important;
  }

  > *:last-child {
    margin-bottom: 0 !important;
  }

  ul {
    list-style: none;
    margin: 15px 0;
    padding: 0;

   &> :first-child {
      padding-right: 15px;
    }
  }
  li {
    // remove column margin or padding
    display: inline-block;
    margin: 0;
    padding: 0;
  }
  li a {
    text-decoration: none;
    border-bottom: 1px solid #ddd;

    &:hover,
    &:focus {
      color: @m-gray-dark;
      text-decoration: none;
      border-bottom: 1px solid @m-gray-dark;
    }
  }
}
